
.maptype-container ul {
  position: relative;
  overflow: auto;
  padding: 5px;
  background-color: rgba(140,140,140,0.3);
}
.maptype-container ul:hover .mapTypeCard {
  width: 80px;
  margin-right: 10px;
  background-size: 100% 100%;
}
.maptype-container ul:hover .mapTypeCard span {
  display: inline;
}
.maptype-container ul .mapTypeCard {
  position: relative;
  height: 60px;
  width: 10px;
  float: left;
  background-size: 100% 100% !important;
  cursor: pointer;
  transition: width 0.2s ease-out;
  list-style-type: none;
  text-align: right;
}
.maptype-container ul .mapTypeCard span {
  background-color: #3385ff;
  display: none;
}
.maptype-container ul .active {
  width: 80px;
}
.maptype-container ul .active span {
  display: inline;
}
.maptype-container ul .normal {
  background: #999 url("../image/map/maptype/shiliang.png") no-repeat;
}
.maptype-container ul .earch {
  background: #999 url("../image/map/maptype/dixing.png") no-repeat;
}
.maptype-container ul .terrain {
  background: #999 url("../image/map/maptype/yingxiang.png") no-repeat;
}


