
.o-box {
  height: 200px;
}
.o-box .plugin-arrow-overview {
    position: absolute;
    top: 0;
    width: 15px;
    height: 15px;
    background-image: url(../image/button_bottom_active.png);
    z-index: 100;
    cursor: pointer;
}
.o-box .top-arrow-overview {
    position: absolute;
    top: 0;
    width: 15px;
    height: 15px;
    background-image: url(../image/button_top_active.png);
    z-index: 100;
    cursor: pointer;
}
.o-box .overview-container {
    position: relative;
    width: 100%;
    height: 100%;
    background-color: #666;
}
.o-box .overview-container .esriOverviewMap,
    .o-box .overview-container .overview-box {
      width: 100%;
      height: 100%;
      background-color: #666;
}
.o-box .overview-container .esriOverviewMap .ovwContainer,
      .o-box .overview-container .overview-box .ovwContainer {
        width: 100% !important;
        height: 100% !important;
}



