
.locationboard-container {
  position: relative;
  width: 100%;
  height: 100%;
  margin: 15px 15px 0 15px;
  color: #fff;
}
.locationboard-container .location-list {
    list-style: none;
    cursor: pointer;
}
.locationboard-container .location-list .location-item {
      margin: 10px;
      -webkit-user-select: none;
         -moz-user-select: none;
          -ms-user-select: none;
              user-select: none;
}



