
.map-container {
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 100%;
}
.map-container .map-box {
  width: 100%;
  height: 100%;
  color: #fff;
}
.map-container .map-box .tooltip {
  color: #000;
}
.map-container .map-box .esriControlsBR {
  display: none;
}
.map-container .map-box .toolbar-wrapper {
  position: absolute;
  display: none;
  right: 20px;
  top: 30px;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-shadow: 0 0 10px #666;
  z-index: 1;
}
.map-container .map-box .toolbar-wrapper .toolbar-list {
  list-style: none;
  background-color: #0a2f4d;
}
.map-container .map-box .toolbar-wrapper .toolbar-list .toolbar {
  vertical-align: middle;
  display: table-cell;
  padding: 10px 5px;
  cursor: pointer;
}
.map-container .map-box .barboard-wrapper {
  position: absolute;
  background-color: #0a2f4d;
  width: 100%;
  margin-top: 10px;
}
.map-container .map-box .maptype-wrapper {
  position: absolute;
  display: none;
  left: 20px;
  top: 100px;
  box-shadow: 0 0 10px #666;
  z-index: 1;
}
.map-container .map-box .legend-wrapper {
  position: absolute;
  width: 170px;
  height: 200px;
  display: none;
  left: 20px;
  bottom: 5px;
  box-shadow: 0 0 10px #666;
  z-index: 1;
  overflow-y: auto;
  background-color: #0a162c;
}
.map-container .map-box .overview-wrapper {
  position: absolute;
  display: none;
  right: 5px;
  bottom: 5px;
  width: 300px;
  height: 200px;
  box-shadow: 0 0 10px #666;
  z-index: 1;
}
.all-screen {
  position: fixed !important;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  height: 100%;
  z-index: 100;
}
.all-screen .map-box .toolbar-wrapper {
  right: 10px !important;
}
.all-screen .map-box .maptype-wrapper {
  left: 10px !important;
}
.all-screen .map-box .legend-wrapper {
  left: 10px !important;
}
.all-screen .map-box .overview-wrapper {
  right: 10px !important;
}


