
.search-board {
  position: relative;
  width: 100%;
  text-align: center;
}
.search-board #search-input #input-keyword {
    margin: 5px;
    line-height: 25px;
}
.search-board #search-result {
    text-align: left;
}
.search-board #search-result .poi-list .poi-item {
      padding: 0 10px;
      cursor: pointer;
}
.search-board #search-result .poi-list .poi-item span {
        line-height: 25px;
        color: #fff;
}
.search-board #search-result .poi-list .poi-item span .icon {
          display: inline-block;
          margin: 0 5px;
          width: 21px;
          height: 31px;
          background: url(../../image/map/search/PointLabel.png) no-repeat;
}



