
.legend-box {
  height: 100%;
}
.plugin-arrow-legend {
  position: absolute;
  right: 0;
  top: 0;
  width: 15px;
  height: 15px;
  background-color: #0a162c;
  background-image: url(../image/button_bottom_active.png);
  z-index: 1;
  cursor: pointer;
}
.top-arrow-legend {
  position: absolute;
  right: 0;
  top: 0;
  width: 15px;
  height: 15px;
  background-color: #0a162c;
  background-image: url(../image/button_top_active.png);
  z-index: 1;
  cursor: pointer;
}
.legend-container {
  position: relative;
  width: 100%;
  height: 100%;
}
.legend-container .legend {
    height: 100%;
    background: linear-gradient(to bottom, #0a162c, #0a2f4d);
}
.legend-container .legend .esriLegendServiceLabel {
      padding: 5px;
      line-height: 1.5;
}
.slide-fade-enter-active,
.slide-fade-leave-active {
  -webkit-animation: all 1.3s ease;
          animation: all 1.3s ease;
}
.slide-fade-enter,
.slide-fade-leave-active {
  -webkit-animation: all 1.3s ease;
          animation: all 1.3s ease;
  height: 0;
  width: 0;
}



