
.isearch-board {
  position: absolute;
  left: 500px;
  top: 150px;
  border-radius: 8px;
  height: 400px;
  overflow: auto;
  width: 500px;
  background-color: white;
  z-index: 10;
}
.isearch-board .info-window-title {
    position: relative;
    height: 46px;
    background-color: white;
    background: linear-gradient(to top, #1f3559, #172234);
    filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#0a162c', endColorstr='#0a2f4d', GradientType=0);
    font-size: 16px;
    color: #fff;
}
.isearch-board .info-window-title .info-window-title-value {
      position: absolute;
      left: 0px;
      line-height: 46px;
      padding-left: 18px;
}
.isearch-board .info-window-title .info-window-close {
      position: absolute;
      right: 13px;
      width: 24px;
      height: 46px;
      padding: 0 5px;
      background-image: url(../../image/close.png);
      background-repeat: no-repeat;
      background-position: center;
}
.isearch-board .info-window-layer-select-Content {
    position: relative;
    height: 40px;
    padding: 0 45px 0 18px;
    background: #E4E8EB;
}
.isearch-board .info-window-layer-select-Content .info-window-layer-select {
      width: 100%;
      border: 0;
      height: 40px;
      padding: 0;
      background: #E4E8EB;
      outline: none;
      font-size: 14px;
      color: #1e2e4a;
}
.isearch-board .info-window-layer-select-Content .info-window-layer-select-icon-spread {
      position: absolute;
      top: 0px;
      right: 13px;
      width: 24px;
      height: 40px;
      padding: 0 5px;
      background-image: url(../../image/spread.png);
      background-repeat: no-repeat;
      background-position: center;
}
.isearch-board .info-window-layer-select-Content .info-window-layer-select-icon-fold {
      position: absolute;
      top: 0px;
      right: 13px;
      width: 24px;
      height: 40px;
      padding: 0 5px;
      background-image: url(../../image/fold.png);
      background-repeat: no-repeat;
      background-position: center;
}
.isearch-board .info-window-layer-select-list {
    position: absolute;
    width: 100%;
    max-height: 200px;
    top: 86px;
    margin: 0;
    padding: 0;
    background: #1e2e4a;
    overflow: auto;
    opacity: 0.9;
    font-size: 14px;
    color: #fff;
}
.isearch-board .info-window-layer-select-list .list-item {
      height: 32px;
      line-height: 32px;
      padding: 0 18px;
}
.isearch-board .info-window-layer-select-list .list-item:hover {
        background: #e5e8eb;
        color: #1e2e4a;
        cursor: pointer;
}
.isearch-board .info-window-layer-select-list .list-item .show-info {
        display: inline-block;
        width: 100%;
        height: 100%;
}
.isearch-board .info-window-content {
    height: 314px;
    overflow: auto;
}
.isearch-board .info-window-content .info-window-content-ul {
      margin: 0;
      padding: 0;
      list-style-type: none;
      font-size: 14px;
      color: #333;
}
.isearch-board .info-window-content .info-window-content-ul .info-item {
        height: 32px;
        line-height: 32px;
        padding: 0 38px;
        font-size: 0;
}
.isearch-board .info-window-content .info-window-content-ul .info-item .field-key {
          display: inline-block;
          width: 30%;
          font-size: 14px;
          white-space: nowrap;
          overflow: hidden;
          text-overflow: ellipsis;
}
.isearch-board .info-window-content .info-window-content-ul .info-item .field-value {
          display: inline-block;
          width: 70%;
          border-left: 1px solid #E4E8EB;
          box-sizing: border-box;
          white-space: nowrap;
          font-size: 14px;
          overflow: hidden;
          text-overflow: ellipsis;
          padding-left: 20px;
}



